*{
  margin: 0;
  padding: 0;
}
a{
  text-decoration: none;
}
body{
  font-family: 'Lato', sans-serif;
  background-color: #FFFFFF;
  font-size: 20px;
}
header{
  background-color:#4c5c69; 
  position: fixed;
  height: 6em;
  width: 100%;
}
.header_container{
  width: 48.9em;
  margin: 0 auto; 
  display: flex;
  justify-content: space-between;
}
.logo{
  float: left;
  padding-top: 1em;
}
.nav{
  float: right;
  padding-top: 2em;

}
.nav a{
  margin-right: 2em;
  color: #FFFFFF;
  font-size: 0.9em;
  font-style: normal;
}
.nav .fa-bars{
  display: none;
}
.nav a:nth-child(5){
  margin-right: 0;
}
.nav a:hover{
  border-bottom: 0.1em solid #2f3942;
}
.nav a:active{
  border-bottom: 0.1em solid #2f3942;
}
#main{
  position: static;
  padding-top: 6em;
  width: 48.9em;
  margin: 0 auto; 
}
.main_container{
  padding-top: 1.5em;

}
.projects{
  padding-bottom: 2.5em;
  border-bottom: 0.05em solid #e8e8e8;
}
h1{
  font-size: 1em;
  font-style: normal;
  color: #4c5b66;
  padding-bottom: 1em;
}
.fa-cogs{
  font-size: 20px;
  color: #297e39;
}

.images{ 
  display: flex;
  justify-content: space-between;
}
.for970{
  display: flex; 
}
.for970 div{
  padding-right: 1.35em;
} 

.images img{
  border: 0.05em solid #f2f2f2;
  padding: 0.15em;
  box-shadow: 0em 0.05em 0 0.05em  #d9d9d9;
}

.feed{
  display: flex;
  padding-bottom: 2.5em;
  border-bottom: 0.05em solid #e8e8e8;
}
div.blog{
  width: 50%;
}
.fa-rss{
  font-size: 20px;
  color: #e29f49;
}
.review, .appcompany{
  display: flex;
}
.blog img{
  border: 0.05em solid #f2f2f2;
  padding: 0.15em;
  box-shadow: 0em 0.05em 0 0.05em  #d9d9d9;
}
.blog h4{
  font-style: normal;
  font-size: 0.75em;
  color: #5c5c5c;
}
.twitter p, .blog p{
  font-style: normal;
  font-size: 0.65em;
  color: #888888;
  line-height: 1.6;
}
.blog a{
  color: #3c92d0;
}
.bottomlink{
  color: #3e95d2;
  text-align: left;
  font-size: 0.65em;
}
.text{
  padding-left: 1.5em;
}
p.data{
  font-size: 0.55em;
  color: #b0b0b0;
}
.appcompany{
  padding-top: 1em;
}
.twitter{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
div.twitter{
  width: 50%;
  padding-left: 10em;
}
.fa-twitter{
  font-size: 20px;
  color: #386bb1;
}
.name{
  color: #515151;
}
p.data2{
  font-size: 0.55em;
  color: #b0b0b0;
}
.twitter a{
  color: #3f97d3;
}
.tweets{
  padding-bottom: 1em;
}
#footer{
  position: static;
  margin: 0 auto;
  width: 48.9em;
}
.footercontent{
  display: flex;
  justify-content: space-between;
}
.icons {
  font-size: 1.4em;
  text-align: right;
}
.copyright{
  font-style: normal;
  font-size: 0.65em;
  color: #8a8a8a;
  align-self: center;
}
.fa-snapchat-square{
  color: #fffc00;
}
.fa-spotify{
  color: #1ED761;
}
.fa-soundcloud{
  color: #ff8800;
}
.fa-youtube{
  color: #ff0000;
}



@media screen and (max-width: 60.625em){
  
  .nav a{
    display: none;
  }
  .nav a.fa-bars{
    display: block;
    float: right;
    color: #FFFFFF;
    font-size: 2.4em;
    margin-right: 1em;
  }
  .header_container{
    width: 100%;
    display: flex;
  }
  .nav{
    margin-left: 10.5em;
  }


  h1, h2{
    font-size: 1.3em;
  }
  .images{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
  }
  .feed{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .blog{
    display: block;
    padding-bottom: 1em;
    justify-content: center;
  }
  .twitter{
    display: block;
  }
  div.twitter{
    padding-left: 0;
  }
}